<template>
    <div class="tabbar">
      <ul class="tabbar-list">
        <li class="tabbar-item">
          <i class="iconfont icon-shop"></i>
          <p>店铺</p>
        </li>
        <li class="tabbar-item">
          <i class="iconfont icon-service"></i>
          <p>客服</p>
        </li>
        <li class="tabbar-item">
          <i class="iconfont icon-collect"></i>
          <p>收藏</p>
        </li>
      </ul>
      <div class="tabbar-button">
        <span class="cart">加入购物车</span>
        <span class="buy">立即购买</span>
      </div>
    </div>
</template>

<script>
  export default {
    name: 'ProductFooter',
    data() {
      return {
      };
    }
  };
</script>
<style lang="scss" scoped>
  @import "~@/assets/scss/mixins";
  .tabbar{
    height: 50px;
    width: 100%;
    background-color: $bgc-theme;
    @include flex-center();
    &-list{
      flex: 1;
      @include flex-around();
      .tabbar-item{
        text-align: center;
        p{
          height: 25px;
          line-height: 25px;
        }
      }
    }
    &-button{
      flex: 1;
      @include flex-center();
      height: 100%;
      color: #fff;
      span{
        text-align: center;
        flex: 1;
        display: inline-block;
        height: 100%;
        line-height: 50px;
      }
      .cart{
        background-color: #ff9200;
      }
      .buy{
        background-color: $header-bgc-translucent;
      }
    }
    .change-color{
      background-color: $header-bgc-translucent;
    }
  }
</style>



