<template>
  <div class="swiper-wrapper">
    <lu-loading v-if="!sliders.length"></lu-loading>
    <lu-slider
      :data = "sliders"
      :direction="direction"
      :loop="loop"
      :interval="interval"
      :autoplay="autoplay"
    >
      <swiper-slide v-for="(item, index) in sliders" :key="index">
        <img :src="item" alt="" class="slider-img">
      </swiper-slide>
    </lu-slider>
  </div>
</template>

<script>
  import LuSlider from '@/base/slider';
  import LuLoading from '@/base/loading';
  import {swiperSlide} from 'vue-awesome-swiper';
  import {sliderOptions} from './config.js';
  import {getProductContent} from '@/api/product.js';
  export default {
    name: 'ProductSlider',
    components: {
      LuSlider,
      swiperSlide,
      LuLoading
    },
    data() {
      return {
        // 滚动方向-水平
        direction: sliderOptions.direction,
        // 无缝滚动
        loop: sliderOptions.loop,
        // 滚动时间间隔
        autoplay: sliderOptions.autoplay,
        interval: sliderOptions.interval,
        sliders: [] // 数据通过ajax获取
      };
    },
    methods: {
      getProduct() {
        return getProductContent().then(data => {
          this.sliders = data.picUrl;
        });
      }
    },
    created() {
      this.getProduct();
    }
  };
</script>
<style lang="scss" scoped>
  @import "~@/assets/scss/mixins";
  .swiper-wrapper{
    width: 100%;
    height: 414px;
    overflow: hidden;
  }
  .slider-img{
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
</style>



