<template>
  <transition name="product">
    <div class="product">
      <lu-navbar class="product-header">
        <i class="iconfont icon-back" slot="left" @click="backto"></i>
        <span class="header-text" slot="center">商品详情</span>
        <i class="iconfont icon-cart" slot="right"></i>
      </lu-navbar>
      <div class="product-content">
        <lu-scroll
          class="product-scroll"
          :data="productDetails"
        >
          <product-slider/>
          <product-details @loaded="productDetailsChange"></product-details>
        </lu-scroll>
      </div>
      <product-footer class="product-footer"></product-footer>
    </div>
  </transition>
</template>

<script>
  import LuScroll from '@/base/scroll';
  import LuNavbar from '@/base/navbar';
  import ProductFooter from './footer.vue';
  import ProductSlider from './slider.vue';
  import ProductDetails from './details.vue';
  export default {
    name: 'product',
    components: {
      LuScroll,
      LuNavbar,
      ProductSlider,
      ProductDetails,
      ProductFooter
    },
    data() {
      return {
        productDetails: {}
      };
    },
    methods: {
      backto() {
        this.$router.back();
      },
      getParams() {
        console.log(this.$route.params);
      },
      productDetailsChange (data) {
        this.productDetails = data;
      }
    },
    created() {
      this.getParams();
    }
  };
</script>

<style scoped lang="scss">
  @import '~@/assets/scss/mixins';
  .product{
    background-color: $bgc-theme;
    width: 100%;
    height: 100%;
    overflow: hidden;
    .product-header{
      background: $header-bgc-translucent;
      color: #fff;
      font-size: $font-size-l;
      z-index: $tabbar-z-index;
      .header-text{
        width: 100%;
        display: inline-block;
        text-align: center;
      }
    }
    .product-content{
      height: 100%;
      width: 100%;
      overflow: hidden;
      position: relative;
      padding-top: 50px;
    }
    &-footer{
      position: fixed;
      bottom: 0;
      z-index: $tabbar-z-index + 1;
    }
    &-scroll{
      width: 100%;
      position: absolute;
      top: 0;
    }
  }
  .product-enter-active,
  .product-leave-active{
    transition: all 0.3s;
  }
  .product-enter,
  .product-leave-to{
    transform: translate3d(100%, 0, 0);
  }
</style>



