<template>
  <div>
    <lu-loading v-if="!productDetails.hasData"></lu-loading>
    <div class="judge-wrapper" v-else>
      <div class="product-title">
        <h4>
          <span class="price">¥{{productDetails.price}}</span>
          <span class="sale">已售{{productDetails.sale}}件</span>
        </h4>
        <p class="title">{{productDetails.title}}</p>
        <p class="logistics">
          <span
            v-for="(item, index) in productDetails.logistics"
            :key="index"
          >{{item}}</span>
        </p>
      </div>
      <div class="product-judge">
        <h4 class="title">商品评价{{productDetails.judge.title}}</h4>
        <div class="judge-category">
          <span
            v-for="(item, index) in productDetails.judge.judgeCategory"
            :key="index"
          >{{item}}</span>
        </div>
        <div class="user">
          <div class="user-id">
            <img :src="productDetails.judge.judgeUser.pic">
            <span class="judge-use">{{productDetails.judge.judgeUser.name[0]}}</span>
          </div>
          <div class="user-text">{{productDetails.judge.judgeList[0]}}</div>
          <div class="user-model">{{productDetails.judge.goodsModel}}</div>
        </div>
      </div>
      <div class="product-details">
        <div class="product-shop">
          <img :src="productDetails.shop.pic" class="shop-pic">
          <span class="shop-name">{{productDetails.shop.name}}</span>
          <div class="shop-score">
            <span
              v-for="(item, index) in productDetails.shop.score"
              :key="index">{{item}}</span>
          </div>
        </div>
        <ul class="product-pic">
          <li
            v-for="(item, index) in productDetails.goodsPic"
            :key="index"
            class="pic-item"
          >
            <img :src="item" alt="">
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import {getProductContent} from '@/api/product.js';
  import LuLoading from '@/base/loading';
  export default {
    name: 'ProductJudge',
    components: {
      LuLoading
    },
    data () {
      return {
        productDetails: {
          hasData: false
        }
      };
    },
    methods: {
      getProductDetails() {
        return getProductContent().then(data => {
          this.productDetails = data;
          this.productDetails.hasData = true;
          this.$emit('loaded', this.productDetails);
          console.log(this.productDetails);
        });
      }
    },
    created() {
      this.getProductDetails();
    }
  };
</script>
<style lang="scss" scoped>
@import "~@/assets/scss/mixins";
  .judge-wrapper{
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
    .product-title{
      padding: 0 8px;
      width: 100%;
      height: 70px;
      background-color: #fff;
      margin-bottom: 10px;
      h4{
        height: 30px;
        line-height: 30px;
        .price{
          color: rgb(222, 24, 27);
          font-size: $font-size-l + 2px;
        }
        .sale{
          font-size: $font-size-base;
          position: absolute;
          right: 8px;
        }
    }
      .title{
        font-size: $font-size-base;
        color: #000;
        height: 20px;
        line-height: 20px;
        @include ellipsis();
      }
      .logistics{
        @include flex-between();
        line-height: 20px;
        height: 20px;
      }
    }
    .product-judge{
      width: 100%;
      height: 100%;
      background-color: #fff;
      overflow: hidden;
      padding: 0 8px;
      .title{
        fone-size: $font-size-base;
        height: 30px;
        line-height: 30px;
      }
      .judge-category{
        line-height: 20px;
        overflow: visible;
        span{
          background-color: #ffededed;
          border-radius: 10px;
          margin-right: 10px;
          padding: 0 5px
        }
      }
      .user{
        width: 100%;
        height: 100%;
        &-id{
          height: 30px;
          line-height: 20px;
          margin-top: 10px;
          img{
            width: 20px;
            height: 20px;
            border-radius: 50%;
          }
        }
        .user-text{
          color: #000;
          padding-left: 10px;
          line-height: 16px;
        }
        .user-model{
          padding-left: 10px;
          line-height:20px;
        }
      }
    }
    .product-details{
      margin-top: 10px;
      padding: 10px 8px;
      width: 100%;
      background-color: #fff;
      .shop-pic{
        width: 40px;
        height: 30px;
      }
      .shop-name{
        line-height: 30px;
      }
      .shop-score{
        @include flex-center();
        padding-top: 10px;
        span{
          flex: 1;
          text-align: center;
        }
      }
      .product-pic{
        width: 100%;
        .pic-item{
          width: 100%;
          height: 359px;
          img{
            width: 100%;
          }
        }
      }
    }
  }
</style>



